  left: 50%;
  transform: translateX(-50%); }

.centery {
  position: absolute;
  top: 50%;
  transform: translateY(-50%); }

.centerxy, .container__block {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%); }

